<template>
    <div class="bscroll" ref="bscroll">
        <div class="bscroll-content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import { DETAIL_BSCROLL_REFRESH, BSCROLL_INIT } from '@/common/const'
// 
import BScroll from '@better-scroll/core'
// https://better-scroll.github.io/docs/zh-CN/guide/base-scroll-api.html#%E4%BA%8B%E4%BB%B6
export default {
    name: 'Scroll',
    props: {
        probeType: {
            type: Number,
            default: 0
        },
        pullUpLoad: {
            type: Boolean,
            default: false
        },
        scrollStart:{
            type: Function,
            default: function() {}
        },
        scrollListen: {
            type: Function,
            default: function (positon) {
                // console.log(positon)
            }
        },
        scrollEnd: {
            type: Function,
            default: function() {}
        },
        pullingUpListen: {
            type: Function,
            default: function () {
                console.log('pullingUp')
            }
        }

    },
    data() {
        return {
            scroll: null,
        }
    },
    mounted() {

        // 0.BScroll
        // this.$bus.$on(BSCROLL_INIT,()=> {
        //     console.log("better-scroll")
        //     this.initScroll()
        // })

        // BScroll
        this.scroll = new BScroll(this.$refs.bscroll, {
            click: true,
            probeType: this.probeType,
            pullUpLoad: this.pullUpLoad
        });
        // 
        this.scroll.on('scrollStart', this.scrollStart)

        //  probeType  1|2|3 0 scroll
        if (this.probeType === 2 || this.probeType === 3) {

            this.scroll.on('scroll', this.scrollListen)
        }
        // 
        if (this.pullUpLoad) {

            this.scroll.on("pullingUp", this.pullingUpListen)
        }
        // 
        this.scroll.on('scrollEnd', this.scrollEnd)

        // scroll
        this.$bus.$on(DETAIL_BSCROLL_REFRESH, this.refresh)

    },
    methods: {
        scrollTo(x, y, time = 300) {
            this.scroll && this.scroll.scrollTo(x, y, time)
        },
        finishPullUp() {
            this.scroll && this.scroll.finishPullUp()
        },
        getY() {
            return this.scroll ? this.scroll.y : 0
        },
        refresh() {
            this.scroll && this.scroll.refresh()
        }
    }
}
</script>

<style lang="scss" scoped>
.bscroll {
    width: 100%;
    // height: calc(100% -  3.2rem);
    // height: 100%;
    overflow: hidden;
    z-index: 100;
}
</style>